.carousel{
    width: 40%;
    margin: auto;
    color: Black;
    border-radius: 8px;  
}

#primer-titulo{
    margin-top:1.5%;
    margin-bottom:3%;
    background-image: linear-gradient(to top, #f77062 0%, #fe5196 100%);
    color: white;
    font-weight:bold;      
    padding: 4%;
}
.caption,h3{
    font-size: 130%;   
}
.caption{
    font-style: italic;
}
h3{
    font-weight:bold;
}
.carousel-item{
    height: 20%;
    border-radius: 15px; 
    border: 8px solid #f25278;
}

#main-carousel{
    background-image: url('../images/colors.jpg');
    width:100%;
    height: 140vh;
}

@media all and (min-width: 600px)and (max-width: 800px){
    #main-carousel{
        background-image: url('../images/colors.jpg');
        height:200%;
        width:200%;
    }   
    .carousel-item{
        height: 100%;
        border-radius: 15px; 
        border: 3px solid #f25278;
    }
    .caption,h3{
        font-size: 90%;   
    }
}
